.popup__input-error {
  color: #FF0000;
  width: 100%;
  font-weight: normal;
  font-size: 12px;
  line-height: 15px;
  position: absolute;
  top: calc(100% + 5px);
  left: 0;
}

@media screen and (max-width: 500px) {
  .popup__input-error {
    font-size: 10px;
    line-height: 10px;
  }
}
