@import url(_invalid/popup__input_invalid.css);

.popup__input {
  width: 100%;
  padding: 0 0 14px 0;
  margin: 50px 0 0;
  border: 0;
  border-bottom: 1px solid #00000033;
}


@media screen and (max-width: 500px) {
  .popup__input {
    margin: 36px 0 0;
    padding: 0 0 12px 0;
    font-size: 12px;
    line-height: 15px;
  }
}